export default function kindAnimes(props: string | undefined) {
  switch (props) {
    case "tv":
      return " ";
      break;

    case "movie":
      return "";
      break;

    case "ova":
      return "OVA";
      break;

    case "ona":
      return "ONA";
      break;

    case "special":
      return "";
      break;

    case "tv_special":
      return "-";
      break;

    case "music":
      return "";
      break;

    case "pv":
      return "PV";
      break;

    case "cm":
      return "CM";
      break;

    default:
      break;
  }
}
